import './signin.css';
import { form } from '../../../../components/forms/form';

export const createSignInView = () => {
    const signInForm = form([
        `<div class="form-group">
            <input type="email" name="email" placeholder="Email" autocomplete="username" required>
        </div>`,
        `<div class="form-group">
            <input type="password" name="password" placeholder="–ü–∞—Ä–æ–ª—å" autocomplete="current-password" required>
        </div>`,
        `<div class="form-group">
            <button type="submit" class="submit-btn">–í–æ–∏ÃÜ—Ç–∏</button>
        </div>`,
    ], 'login-form');

    const container = document.createElement('div');
    container.classList.add('form-container');

    const wrapper = document.createElement('div');
    wrapper.classList.add('form-wrapper');

    const header = document.createElement('div');
    header.classList.add('form-header');
    const h1 = document.createElement('h1');
    h1.textContent = '–í—Ö–æ–¥';
    header.append(h1);

    const footer = document.createElement('div');
    footer.classList.add('form-footer');
    const p = document.createElement('p');
    p.innerHTML = '–ù–µ—Ç –∞–∫–∫–∞—É–Ω—Ç–∞? <a href="#/register" class="link">–ó–∞—Ä–µ–≥–∏—Å—Ç—Ä–∏—Ä–æ–≤–∞—Ç—å—Å—è</a>';
    footer.append(p);

    wrapper.append(header, signInForm, footer);
    container.append(wrapper);
    return container;
};

