import { clearSession } from "../../../../services/localStorage/authService";
import { actions as stateActions } from "../../../../storage/stateApp.js";

export const createUserMenu = (items) => {
    const { clearUser } = stateActions;
    const menuContainer = document.createElement('div');
    menuContainer.classList.add('user__menu');

    // –ò–∫–æ–Ω–∫–∞-—Ç—Ä–∏–≥–≥–µ—Ä
    const trigger = document.createElement('button');
    trigger.type = 'button';
    trigger.classList.add('user__menu_trigger');
    trigger.setAttribute('aria-haspopup', 'true');
    trigger.setAttribute('aria-expanded', 'false');
    trigger.textContent = 'üë§';

    const ul = document.createElement('ul');
    ul.classList.add('user__menu__wrapper');
    ul.hidden = true;

    // –ö–∞—Ä—Ç–∞ –¥–µ–π—Å—Ç–≤–∏–π –ø–æ –∫–ª—é—á—É
    const actions = {
        –ø—Ä–æ—Ñ–∏–ª—å: () => console.log('–û—Ç–∫—Ä—ã—Ç—å –ø—Ä–æ—Ñ–∏–ª—å'),
        –Ω–∞—Å—Ç—Ä–æ–π–∫–∏: () => console.log('–û—Ç–∫—Ä—ã—Ç—å –Ω–∞—Å—Ç—Ä–æ–π–∫–∏'),
        –≤—ã—Ö–æ–¥: () => {
            clearSession();
            clearUser();
            window.location.hash = '#/home';
        }
    };

    const fragment = document.createDocumentFragment();

    items.forEach(text => {
        const li = document.createElement('li');
        li.classList.add('user__menu_item');
        li.textContent = text;
        li.dataset.menu = text;

        li.addEventListener('click', () => {
            const action = actions[text];
            if (action) {
                action();
            } else {
                console.warn(`–ù–µ—Ç –¥–µ–π—Å—Ç–≤–∏—è –¥–ª—è –ø—É–Ω–∫—Ç–∞: ${text}`);
            }
        });

        fragment.append(li);
    });

    ul.append(fragment);
    // –¢–æ–≥–≥–ª–∏–Ω–≥
    const toggle = () => {
        const isHidden = ul.hidden;
        ul.hidden = !isHidden;
        trigger.setAttribute('aria-expanded', String(!isHidden));
    };
    trigger.addEventListener('click', (e) => {
        e.stopPropagation();
        toggle();
    });
    // –ó–∞–∫—Ä—ã—Ç–∏–µ –ø–æ –∫–ª–∏–∫—É –≤–Ω–µ
    document.addEventListener('click', (e) => {
        if (!menuContainer.contains(e.target)) {
            ul.hidden = true;
            trigger.setAttribute('aria-expanded', 'false');
        }
    });

    menuContainer.append(trigger, ul);
    return menuContainer;
};


