import "./signup.css"
import { form } from "../../../../components/forms/form"

export const createSignUpView = () => {
    const signUpForm = form([
        `<div class="form-group">
            <input type="text" id="fullName" name="fullName" required placeholder="–ü–æ–ª–Ω–æ–µ –∏–º—è">
        </div>`,
        `<div class="form-group">
            <input type="email" id="email" name="email" required placeholder="Email">
        </div>`,
        `<div class="form-row">
            <div class="form-group">
                <input type="password" id="password" name="password" required placeholder="–ü–∞—Ä–æ–ª—å">
            </div>
            <div class="form-group">
                <input type="password" id="confirmPassword" name="confirmPassword" required placeholder="–ü–æ–¥—Ç–≤–µ—Ä–¥–∏—Ç–µ –ø–∞—Ä–æ–ª—å">
            </div>
        </div>`,
        `<div class="form-group">
            <input type="text" id="company" name="company" placeholder="–ö–æ–º–ø–∞–Ω–∏—è (–æ–ø—Ü–∏–æ–Ω–∞–ª—å–Ω–æ)">
        </div>`,
        `<div class="form-group checkbox-group">
            <label class="checkbox-label"></label>
                <input type="checkbox" id="terms" name="terms" required>
                <span class="terms-text">–°–æ–≥–ª–∞—Å–µ–Ω —Å <a href="#">—É—Å–ª–æ–≤–∏—è–º–∏</a></span>
        </div>`,
        `<button type="submit" class="submit-btn">–°–æ–∑–¥–∞—Ç—å –∞–∫–∫–∞—É–Ω—Ç</button>`,
    ], 'register-form');

    const container = document.createElement('div');
    container.classList.add('form-container');

    const wrapper = document.createElement('div');
    wrapper.classList.add('form-wrapper');

    const header = document.createElement('div');
    header.classList.add('form-header');
    const h1 = document.createElement('h1');
    h1.textContent = '–†–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏—è';
    header.append(h1);

    const footer = document.createElement('div');
    footer.classList.add('form-footer');
    const p = document.createElement('p');
    p.innerHTML = '–£–∂–µ –µ—Å—Ç—å –∞–∫–∫–∞—É–Ω—Ç? <a href="#/login" class="link">–í–æ–π—Ç–∏</a>';
    footer.append(p);

    wrapper.append(header, signUpForm, footer);
    container.append(wrapper);
    return container;
}

